import React from 'react';
import Menu from './menu.svg';
import s from './burgerButton.module.sass';

export default ({
    imgSrc = Menu, onClick = () => {}, animated = ''
}) => <div className={s.UI}>
    <img
        className={animated ? s.animated : ''}
        src={imgSrc}
        alt="menu"
        onClick={onClick}
    />
</div>;