import React from 'react';
import { connect } from 'react-redux';
import Sidebar from '../../ui/Sidebar';
import s from './mainSidebar.module.sass';
import SideMenuItems from '../../ui/SideMenuItems';
import menuItems from './menuItems';

const MainSidebar = ({ visible, toggleSidebar }) => (
  <div className={s.Shared}>
    {/* Menu */}
    <Sidebar
      visible={visible}
      onHide={toggleSidebar}
      content={<SideMenuItems items={menuItems} />}
    />
  </div>
);

const getKeysFromStore = store => ({
  visible: store.sidebar.visible
});
const getEventsFromStore = dispatch => {
  return {
    toggleSidebar: () => dispatch({ type: 'TOGGLE_SHOW_SIDEBAR' })
  };
};

export default connect(
  getKeysFromStore,
  getEventsFromStore
)(MainSidebar);
