import React from 'react';
import { connect } from 'react-redux';
import { Route, Switch, Link, BrowserRouter } from 'react-router-dom';
import { ToastContainer, toast } from 'react-toastify';
// import {ProgressSpinner} from 'primereact/progressspinner';

import STORAGE from './services/STORAGE';

import Header from './shared/Header';
import Sidebar from './shared/MainSidebar';
import MainPage from './pages/Main';
import ProfilePage from './pages/Profile';
import SignInPage from './pages/SignIn';
import SignUpPage from './pages/SignUp';

const Page404 = () => <div>404 not found!</div>;

class Router extends React.Component {
  state = {
    isLoad: false
  };

  async componentWillMount() {
    const { setUserData, user } = this.props;
    const _user = await STORAGE.getUserData();

    if (!user.token) {
      setUserData(_user);
      this.setState({ isLoad: true });
    } else {
      this.setState({ isLoad: true });
    }
  }

  render() {
    const { isLoad } = this.state;

    return (
      <BrowserRouter>
        {!isLoad ? (
          <div>Loading...</div>
        ) : (
          <>
            <Header Link={Link} />
            <Sidebar Link={Link} />
            <Switch>
              <Route
                exact
                path='/'
                component={() => <MainPage Link={Link} />}
              />
              <Route
                exact
                path='/signup'
                component={() => <SignUpPage Link={Link} />}
              />
              <Route
                exact
                path='/signin'
                component={() => <SignInPage Link={Link} />}
              />
              <Route
                exact
                path='/profile'
                component={() => <ProfilePage Link={Link} />}
              />
              <Route exact path='*' component={() => <Page404 />} />
            </Switch>
            <ToastContainer position={toast.POSITION.BOTTOM_RIGHT} />
          </>
        )}
      </BrowserRouter>
    );
  }
}

const getKeysFromStore = store => ({
  user: store.profile.user
});

const getEventsFromStore = dispatch => ({
  setUserData: user => dispatch({ type: 'SET_USER_DATA', payload: user })
});

export default connect(
  getKeysFromStore,
  getEventsFromStore
)(Router);
