import React, { Suspense } from 'react';
import { connect } from 'react-redux';
import { Redirect } from 'react-router-dom';
import userEffects from './effects';
import s from './signin.module.sass';
import TextInput from '../../ui/TextInput';
import Button from '../../ui/Button';

class SignIn extends React.PureComponent {
  state = {
    email: '',
    password: '',
    confirm_password: '',
    errorMessage: '',
    success: false
  };

  onChangeStateKey = key => event => {
    this.setState({ [key]: event.target.value });
  };

  handleSignIn = async () => {
    const { userAuth } = this.props;
    const { email, password } = this.state;

    userAuth({ email, password })
      .then(() => {})
      .catch(error => {
        this.setState({ error: error.message });
      });
  };

  render() {
    const { Link, user, t } = this.props;

    return (user || {}).token ? (
      <Redirect to='/profile' />
    ) : (
      <div className={s.SignIn}>
        <div className={s.SignInInputs}>
          {[
            { name: t('sign_in_email_input'), ph: t('sign_in_email_input') },
            {
              name: t('sign_in_password_input'),
              ph: t('sign_in_password_input')
            }
          ].map((input, index) => (
            <TextInput
              key={index}
              label={`${input.ph}:`}
              placeholder={input.ph}
              type={input.name.includes('password') ? 'password' : 'text'}
              value={this.state[input.name]}
              onChange={this.onChangeStateKey(input.name)}
            />
          ))}
        </div>
        <Button
          className='SignInButton'
          onClick={this.handleSignIn}
          text={t('sign_in_button_title')}
        />
        <hr />
        <div className={s.SignUpCreateAccountHint}>
          {t('sign_in_no_account_hint')}
        </div>
        <Link className={s.SignUpButton} to='/signup'>
          <Button text={t('sign_in_sign_up_switch_title')} />
        </Link>
      </div>
    );
  }
}

const getKeysFromStore = store => ({
  user: store.profile.user,
  t: store.translate.t
});

const getEventsFromStore = userEffects;

export default connect(
  getKeysFromStore,
  getEventsFromStore
)(SignIn);
