import EN from '../languages/en.json';
import RU from '../languages/ru.json';

const languages = { en: EN, ru: RU };

const initialState = {
  language: 'ru',
  t: word => languages[initialState.language][word]
};

export default (state = initialState, action) => {
  switch (action.type) {
    case 'CHANGE_APPLICATION_LANGUAGE':
      return {
        ...state,
        language: action.payload,
        t: word => languages[action.payload][word]
      };
    default:
      return state;
  }
};
