import config from '../config';
import STORAGE from './STORAGE';

const { apiURL, apiVersion } = config;

export default class API {
    constructor(entity = '') {
        this.URL = `${apiURL}${apiVersion ? '/' : ''}${apiVersion}/${entity}`;
    }

    request = async({ url, method, isAuth, body }) => {
        const headers = {};
        const options = { method };
        const token = await STORAGE.getToken();

        if (isAuth) headers.authorization = `Bearer ${token}`;
        if (body) options.body = JSON.stringify(body);

        headers['content-type'] = 'application/json';
        options.headers = headers;

        return fetch(url, options)
            .then(res => res.json())
            .catch(() => ({ error: 'Cannot connect to server'}));
    }

    get = (path = '', isAuth = false, queries = {}) => {
        return this
            .request({ url: `${this.URL}/${path}`, method: 'GET', isAuth });
    }

    post = (path = '', isAuth = false, body = {}) => {
        return this
            .request({ url: `${this.URL}/${path}`, method: 'POST', isAuth, body });
    }

    put = (path = '', isAuth = false, body = {}) => {
        return this
            .request({ url: `${this.URL}/${path}`, method: 'PUT', isAuth, body });
    }

    delete = (path = '', isAuth = false) => {
        return this
            .request({ url: `${this.URL}/${path}`, method: 'DELETE', isAuth });
    }
}