import React from "react";
import { connect } from "react-redux";
// import s from "./profile.module.sass";

class Profile extends React.PureComponent {
  state = {}

  onChangeStateKey = (key) => (event) => {
    this.setState({ [key]: event.target.value });
  }

  render() {
    const { changeLanguage, t, language } = this.props;

    return <>
    {t('profile_title')} <hr />
      {language}
      <button onClick={() => changeLanguage(language === 'ru' ? 'en' : 'ru')}>
        Change Language
      </button>
    </>
  }
}

const getKeysFromStore = store => ({
  language: store.translate.language,
  t: store.translate.t
});
const getEventsFromStore = dispatch => {
  return {
    changeLanguage: (language) => dispatch({
      type: "CHANGE_APPLICATION_LANGUAGE",
      payload: language,
    }),
  };
};

export default connect(
  getKeysFromStore,
  getEventsFromStore
)(Profile);
