import React from 'react';
import usersService from '../../services/users';
import { connect } from "react-redux";
import s from './main.module.sass';

class Main extends React.PureComponent {
    state = {
        usersList: []
    }

    handleGetUsers = async() => {
        const usersList = await usersService.get();

        this.setState({ usersList });
    }

    render() {
        // const { loaded, toggleLoaded } = this.props;
        const { usersList = [] } = this.state;

        return <div className={s.Main}>
            Main Page
            {
                usersList.map((user, index) => {
                    return <div key={index}>{ user.name } | { user.email }</div>
                })
            }
            <button onClick={this.handleGetUsers}>Get users</button>
        </div>;
    }
};

const getKeysFromStore = (store) => ({
	loaded: store.main.loaded,
});
const getEventsFromStore = (dispatch) => {
	return {
		toggleLoaded: () => dispatch({ type: "TOGGLE_MAIN_LOADED" }),
	};
};

export default connect(
    getKeysFromStore,
    getEventsFromStore,
)(Main);
