import React from 'react';
import { connect } from 'react-redux';
import { Redirect } from 'react-router-dom';
import userEffects from './effects';
import TextInput from '../../ui/TextInput';
import Button from '../../ui/Button';
import s from './signup.module.sass';

class SignUp extends React.PureComponent {
  state = {
    email: '',
    name: '',
    password: '',
    confirm_password: '',
    errorMessage: '',
    success: false
  };

  onChangeStateKey = key => event => {
    this.setState({ [key]: event.target.value });
  };

  handleSubmit = async () => {
    const { userRegister } = this.props;
    const { name, email, password } = this.state;

    userRegister({ name, email, password })
      .then(() => {})
      .catch(error => {
        this.setState({ error: error.message });
      });
  };

  render() {
    const { Link, user, t } = this.props;

    return (user || {}).token ? (
      <Redirect to='/profile' />
    ) : (
      <div className={s.SignUp}>
        <div className={s.SignUpInputs}>
          {[
            { name: t('sign_in_email_input'), ph: t('sign_in_email_input') },
            { name: t('sign_in_name_input'), ph: t('sign_in_name_input') },
            {
              name: t('sign_in_password_input'),
              ph: t('sign_in_password_input')
            },
            {
              name: t('sign_in_confirm_password_input'),
              ph: t('sign_in_confirm_password_input')
            }
          ].map((input, index) => (
            <TextInput
              key={index}
              label={`${input.ph}:`}
              placeholder={input.ph}
              // type={input.name.includes('password') ? 'password' : 'text'}
              value={this.state[input.name]}
              onChange={this.onChangeStateKey(input.name)}
            />
          ))}
        </div>
        <Button
          className='SignUpButton'
          onClick={this.handleSubmit}
          text={t('sign_up_button_title')}
        />
        <hr />
        <div className={s.SignInHaveAccountHint}>
          {t('sign_up_has_account_hint')}
        </div>
        <Link className={s.SignInButton} to='/signin'>
          <Button text={t('sign_up_sign_in_switch_title')} />
        </Link>
      </div>
    );
  }
}

const getKeysFromStore = store => ({
  authorized: store.signup.authorized,
  user: store.profile.user,
  t: store.translate.t
});

const getEventsFromStore = userEffects;

export default connect(
  getKeysFromStore,
  getEventsFromStore
)(SignUp);
