import React from 'react';
import { connect } from "react-redux";
import BurgerButton from '../../ui/BurgerButton';
import MenuHorizontal from '../../ui/MenuHorizontal';
import Logo from '../../ui/Logo';
import Button from '../../ui/Button';
import s from './header.module.sass';
import menuItems from './menuItems';

const Header = ({ Link, toggleSidebar, visible }) => <header className={s.Shared}>
    <BurgerButton animated={visible} onClick={toggleSidebar} className={s.Burger} />
    <MenuHorizontal items={menuItems} className={s.UpMenu} />
    <Logo />
    <Button text="LOG IN" Link={Link} to="signin" styles={{ width: '20%' }} />
</header>;

const getKeysFromStore = (store) => ({
	visible: store.sidebar.visible,
});
const getEventsFromStore = (dispatch) => {
	return {
		toggleSidebar: () => dispatch({ type: "TOGGLE_SHOW_SIDEBAR" }),
	};
};

export default connect(
    getKeysFromStore,
    getEventsFromStore,
)(Header);