import { createStore, combineReducers } from "redux";
import TranslateReducer from "./services/translate";
import MainPageReducer from "./pages/Main/reducers";
import SignUpReducer from "./pages/SignUp/reducers";
import ProfileReducer from "./pages/Profile/reducers";
import SidebarReducer from "./shared/MainSidebar/reducer";

const store = createStore(
  combineReducers({
    main: MainPageReducer,
    signup: SignUpReducer,
    profile: ProfileReducer,
    sidebar: SidebarReducer,
    translate: TranslateReducer,
  })
);

export default store;
